<template>
	<div id="knowledge-base-article">
		<div class="DetailsHeader mb-2">
			<b-button variant="flat-danger" @click="GoBack">
				<feather-icon icon="ChevronLeftIcon" class="mr-50" />
				<span class="align-middle">返回教程列表</span>
			</b-button>
		</div>
		<b-row>
			<!--/ related question -->

			<b-col order-md="2">
				<b-card>
					<!-- Title -->
					<b-card-title class="mb-1">
						<feather-icon icon="AwardIcon" size="21" />

						{{ kb_question.title }}
					</b-card-title>
					<b-card-text class="mb-2"> 最后更新：{{ kb_question.updated_at | formatDate }} </b-card-text>

					<!-- Content -->
					<!-- eslint-disable vue/no-v-html -->
					<div class="question-content" v-html="kb_question.body" />
					<!-- eslint-enable -->
				</b-card>
			</b-col>
		</b-row>
	</div>
</template>

<script>
import { BRow, BCol, BCard, BListGroup, BListGroupItem, BCardText, BCardTitle, BBreadcrumb, BButton } from 'bootstrap-vue'
import { formatDate } from '@/libs/date'

export default {
	components: {
		BRow,
		BCol,
		BCard,
		BListGroup,
		BCardText,
		BCardTitle,
		BListGroupItem,
		BBreadcrumb,
		BButton
	},
	filters: {
		formatDate(time) {
			time = time * 1000
			var date = new Date(time)
			return formatDate(date, 'yyyy-MM-dd hh:mm')
		}
	},
	data() {
		return {
			items: [
				{
					text: 'Help'
				}
			],
			kb_question: {}
		}
	},
	mounted() {
		this.Init()
		this.items.push({
			text: '教程',
			acive: true
		})
	},
	methods: {
		Init() {
			this.$http({
				url: '/api/v1/user/knowledge/fetch?id=' + this.$route.params.id,
				method: 'GET'
			}).then(res => {
				res = res.data.data
				this.kb_question = res
			})
		},
		GoBack() {
			this.$router.push({ name: 'help' })
		}
	}
}
</script>

<style lang="scss">
@import '@core/scss/vue/pages/page-knowledge-base.scss';
.DetailsHeader {
	display: flex;
	align-items: center;
	* {
		margin: 0;
	}
}
</style>
